/**
 *
 */
import {$id, $val, $num} from '../utils/dom_utils';
import Field from './Field.js';

/**
 *
 */
class Continuous /*extends Field*/ {
	/**
	 *
	 */
	constructor() {
		// super();
		this.addListeners();
	}

	/**
	 *
	 */
	set vals(cfg){
		this.size 			= cfg.size;
		this.distribution 	= cfg.distribution;
	}

	set size(n) {
		$id('pairs-continuous-num').value 	 = n;
		$id('pairs-continuous-slider').value = n;
	}

	/**
	 *
	 */
	set distribution(str) {
		switch(str) {
			case 'uniform':
				$id('btn-thresholds-dist-uniform').checked 	= true;
				$id('thresholds-dist-normal'	 ).hidden 	= true; 
				$id('thresholds-dist-width' 	 ).hidden 	= true; 
				break;
			case 'normal':
				$id('btn-thresholds-dist-normal').checked 	= true;
				$id('thresholds-dist-normal'	).hidden 	= false; 
				$id('thresholds-dist-width' 	).hidden 	= true; 
				break;
			case 'linear':
				$id('btn-thresholds-dist-width' ).checked 	= true;
				$id('thresholds-dist-normal'	).hidden 	= true; 
				$id('thresholds-dist-width'		).hidden 	= false; 
				break;
			default:
				throw new Error('we should have a distribution');
		}
	}

	/**
	 *
	 */
	get vals() {
		return {
			'size': this.size,
			'distribution': this.distribution
		}
	}

	get size() {
		return $num('pairs-continuous-num');
	}

	/**
	 *
	 */
	get distribution() { 
		return document.querySelector('input[name="btn-grp-thresholds-dist"]:checked').value;
	}

	/**
	 *
	 */
	addListeners() {
		let dispatch = e => Field.dispatcher.dispatchEvent(e);

		$id('pairs-continuous-num'	 ).addEventListener('input',  e => { this.size = e.target.value; dispatch('thresholdsChanged'); });
		$id('pairs-continuous-slider').addEventListener('change', e => { this.size = e.target.value; dispatch('thresholdsChanged'); });

		$id('btn-thresholds-dist-uniform').addEventListener( 'change', e => { this.distribution = 'uniform'; dispatch('thresholdsChanged'); } );
		$id('btn-thresholds-dist-normal' ).addEventListener( 'change', e => { this.distribution = 'normal';  dispatch('thresholdsChanged'); } );
		$id('btn-thresholds-dist-width').addEventListener( 'change', e => { this.distribution = 'linear'; dispatch('thresholdsChanged'); } );
	}
}
export default Continuous;