/**
 *
 */
import pairs from '../../html/pairs.html';

import {$id, $val, $num} from '../utils/dom_utils';
import Field from './Field.js';

/**
 *
 */
class Pairs extends Field {
	/**
	 *
	 */
	constructor() {
		super({ id: 'pairs', html: pairs });

		this.addListeners();
	}

	/**
	 *
	 */
	get vals() {
		return { 
			'pairs': 		this.pairs,
			'lattice': 		this.lattice
		}
	}

	/**
	 *
	 */
	set vals(cfg){
		// console.log(cfg);
		// console.log(cfg.pairs);
		this.pairs 			= cfg.pairs;
		this.lattice 		= cfg.lattice.size;
	}

	/**
	 *
	 */
	get lattice() {
		return {'size': $num('pairs-lattice-num')};
	}

	/**
	 *
	 */
	set lattice(n) {
		$id('pairs-lattice-num').value = n;
		$id('pairs-lattice-slider').value = n;
	}

	/**
	 *
	 */
	get pairs() { 
		return document.querySelector('input[name="btn-pairs"]:checked').value;
		// return $id('btn-pairs-lattice').checked ? 'lattice' : 'continuous'; 
	}

	/**
	 *
	 */
	set pairs(str) {
		switch (str) {
			case 'lattice':
				$id('btn-pairs-lattice').checked = true;
				$id('pairs-lattice'   ).hidden 	 = false;
				$id('pairs-continuous').hidden 	 = true; 
				break;
			case 'continuous':
				$id('btn-pairs-continuous').checked = true;
				$id('pairs-lattice'   ).hidden 	 = true;
				$id('pairs-continuous').hidden 	 = false;
				break;
			default:
				throw new Error('Need threshold pairs string (lattice/continuous),\n\tgot: ' + str);
		}
	}

	/**
	 *
	 */
	addListeners() {
		let dispatch = e => Field.dispatcher.dispatchEvent(e);

		$id('btn-pairs-lattice'		).addEventListener( 'change', e => { this.pairs = 'lattice'; 	dispatch('pairsChanged'); } );
		$id('btn-pairs-continuous' 	).addEventListener( 'change', e => { this.pairs = 'continuous'; dispatch('pairsChanged'); } );

		// BOGUS
		// BOGUS
			// model should maybe only update when size change is finished
			// update only on change but it will update on all intermediate values too
				// if (cfg.supportSize != this.Value()) { cfg.supportSize = this.Value(); }
		$id('pairs-lattice-num'	  ).addEventListener('input',  e => { this.lattice = e.target.value; dispatch('pairsChanged'); });
		$id('pairs-lattice-slider').addEventListener('change', e => { this.lattice = e.target.value; dispatch('pairsChanged'); });

		Field.dispatcher.addEventListener('getThresholdsType', () => {
			dispatch('thresholdsChanged');
			// dispatch('pairsChanged');
		});
	}
}
export default Pairs;