import BoardsVP from './BoardsVP';
import BoardsPV from './BoardsPV';

import BoardIO from './BoardIO.js';
const  BoardI  = require('./BoardI' ).BoardI;

import PreisachPlane  from '../../PreisachPlaneJXG/src/PreisachPlane.js';

require("./boards-style.css");
require("../node_modules/jsxgraph/distrib/jsxgraph.css");

const PROPS	= require('./props_preisach.json');

/**
 * let's imagine for now that when we init the boards we do not maintain a 'draw' state
 */ 
// let props_draw 	= require('./props_draw.json');

let propsIO = {
	"labels": {
		"centre": {
			"x": "Pressure",
			"y": "Volume"
		},
		"right": {
			"x": "input",
			"y": "output"
		}
	}
}
/**
 *
 */
class BoardsManager {
	/**
	 * create a BoardsManager
	 * @constructor BoardsManager
	 * @param {dispatcher} dispatcher - the dispatcher
	 */
	constructor(dispatcher) {
		// let atts = {}
		this.dispatcher = dispatcher;
		this.allBoards = {		
			bpi: new PreisachPlane('jxg-pressure-in', 	PROPS.PROPS_PI, dispatcher),
			bpo: new PreisachPlane('jxg-pressure-out', 	PROPS.PROPS_PO, dispatcher),
			bvi: new PreisachPlane('jxg-volume-in', 	PROPS.PROPS_VI, dispatcher),

			bio: new BoardIO("jxg-InOut", propsIO, dispatcher),
			bi:  new BoardI("jxg-In", dispatcher)
		}
		this.getJC = () => this.allBoards.bio.board.jc;

		this.boards = null;
	}

	/**
	 * @param 
	 * @return
	 */
	init(cfg) {
		if (this.boards != null) {
			this.boards.reset(cfg);
			// this.boards.distBoard.dist.destroy();
		}

		switch(cfg['control-variable']) {
			case 'P':
				console.log(cfg.thresholds);
				this.boards = new BoardsPV(this.allBoards, cfg, PROPS);
				break;
			case 'V':
				this.boards = new BoardsVP(this.allBoards, cfg, PROPS);
				break;
			default:
				throw new Error('BoardManager - need a control variable');
		}		

		return this.boards;
	}
}
export default BoardsManager;