// import {Thresholds_BiStat as Thresholds} from '../Thresholds_BiStat.js';
import Thresholds_BiStat from '../Thresholds_BiStat.js';
import BiFunc from '../BiFunc.js';
/**
 *
 */
class BiPressure extends BiFunc {
	/**
	 *
	 */
	constructor(args) {
		super(args);
		// console.log(args);

		/**
		 * @property output
		 * @type number
		 * need this for the size of the hysterons on the Preisach plane
		 */
		this.output;

		this.thresholds = new Thresholds_BiStat(args.thresholds);

		let closedVol = this.fOn(this.thresholds.low);

		this.fOff = x => closedVol;
	}
	
	update(x) {
		this.output = super.update(x);
		// this.input = x;
		return this.output;
	}
}
export default BiPressure;
