import PreisachPlaneJXG from './PreisachPlaneJXG.js';
import {PPDistHH} from './PPDist.js';

/**
 * @class PreisachPlaneHozHighJXG
 */
class PreisachPlaneHozHighJXG extends PreisachPlaneJXG  {
	/**
	 * @constructor
	 * @extends PreisachPlaneJXG
	 */
	constructor(id, props, dispatcher) {
		super(id, dispatcher);

		let orthos = this.getOrthoPoints(this.point);
		this.orthos = this.board.create('orthos', orthos, {});

		if ( props['dist-board'] ) {
			this.initCheckBoxes(); 
		}
	}

	initCheckBoxes() {
		this.cbHysterons 	= this.board.create('checkbox', [ 0.0, 	0.95, 'Hysterons' 		], PreisachPlaneJXG.CB_PROPS);
		this.cbHystegon 	= this.board.create('checkbox', [ 0.0, 	0.88, 'Hystegon' 		], PreisachPlaneJXG.CB_PROPS);
		this.cbSupport 		= this.board.create('checkbox', [ 0.0, 	0.81, 'Support' 		], PreisachPlaneJXG.CB_PROPS);
		this.cbDistT 		= this.board.create('checkbox', [ 0.0, 	0.74, 'X ~ Thresholds' 	], PreisachPlaneJXG.CB_PROPS);
		this.cbDistW 		= this.board.create('checkbox', [ 0.0, 	0.67, 'X ~ Weights' 	], PreisachPlaneJXG.CB_PROPS);
		// the thing about making distribution a pair of radio buttons is that they are usually both off
		// this.cbDistW.rendNodeCheckbox.type = 'radio';


		this.cbDistT.rendNodeCheckbox.disabled = true;
		this.cbDistW.rendNodeCheckbox.disabled = true;

		super.addListeners();
	}

	init(props_board, props_draw, thresholds, weights) {
		/**
		 * init distribution
		 */
		if ( props_board['dist-board'] ) {
			if ( JXG.exists(this.dist) ) {
				this.dist.destroy();
				this.dist = null;
				// set visible distribution
			}
			this.dist = new PPDistHH( this.board, {thresholds: thresholds, weights: weights}, {});
			super.checked = props_draw;
			
			if ( this.memgon != null ) {
				this.board.removeObject(this.memgon);
			}
			this.memgon = this.board.create('memgonHH', this.dist.support, {});

			// if ( props_board['input-board'] ) {
			// 	this.memgon = this.board.create('memgon', this.dist.support, {});
			// } else {
			// 	this.memgon = this.board.create('memgonHL', this.dist.support, {});
			// }
		}
		
		/**
		 * init point
		 */
		let ptFilename;

		if ( props_board['input-board'] ) {
			ptFilename = 'NES_red.svg';
		} else {
			ptFilename = 'NES_blue.svg';
		}
		super.initPoint(props_board, ptFilename);

		/**
		 * init axes
		 */
		super.initAxes(props_board);
	}

	lowHigh2xy(p) { 
		return { 
			"x": p.high, 
			"y": 1 - p.low
		};
	}

	support2xy(sup) {
		return {
			'bot': [ () => sup.low + sup.diag, () => sup.low            ],
			'top': [ () => sup.high,           () => sup.low             ],
			'sq':  [ () => sup.high,           () => sup.high - sup.diag ] 
		};
	}
	// support2xy(sup) {
		// return [
		// 	[ sup.low + sup.diag,	sup.low 				],
		// 	[ sup.high, 			sup.low 				],
		// 	[ sup.high, 			sup.high - sup.diag 	] 
		// ];
		// }

	/**
	 * @override
	 * @param 
	 * @return
	 */
	getOrthoPoints(x) {
		return {    
			'c': [ [ ()=>x.X(), 1         ], [ ()=>x.X(), ()=>x.X() ] ],
			'w': [ [ 1,         ()=>x.X() ], [ ()=>x.X(), ()=>x.X() ] ],
			'p': [ [ ()=>x.X(), 0         ], [ ()=>x.X(), ()=>x.X() ] ]
		};
	}
}

export default PreisachPlaneHozHighJXG;