import recruitability 	from '../../html/recruitability.html';

import {$id, $val, $num} from '../utils/dom_utils';
import Field from './Field.js';

import noUiSlider from 'nouislider';

const nouiClasses = {
	target: "target",
	base: "base",
	origin: "origin",
	handle: false,
	handleLower: false,
	handleUpper: false,
	touchArea: "touch-area",
	horizontal: "horizontal",
	vertical: false,
	background: "background",
	connect: false,
	connects: false,
	ltr: "ltr",
	rtl: "rtl",
	textDirectionLtr: "txt-dir-ltr",
	textDirectionRtl: "txt-dir-rtl",
	draggable: "draggable",
	drag: "state-drag",
	tap: "state-tap",
	active: "active",
	tooltip: "tooltip",
	pips: "pips",
	pipsHorizontal: "pips-horizontal",
	pipsVertical: "pips-vertical",
	marker: "marker",
	markerHorizontal: "marker-horizontal",
	markerVertical: "marker-vertical",
	markerNormal: "marker-normal",
	markerLarge: "marker-large",
	markerSub: "marker-sub",
	value: "value",
	valueHorizontal: "value-horizontal",
	valueVertical: "value-vertical",
	valueNormal: "value-normal",
	valueLarge: "value-large",
	valueSub: "value-sub"
};

const nouiCfg = {
	orientation: 	"vertical",
	connect: 		true,
	behaviour: 		'drag',
	range: 		{ 
					'min': 0, 
					'max': 1 
				},
	// pips: 	{
				// 	mode: 'count', 
				// 	values: 2
				// },
	// cssPrefix: 'noUi-', // defaults to 'noUi-',
	CssClasses: nouiClasses
}

/**
 *
 */
class Recruitability extends Field {
	/**
	 *
	 */
	constructor() {
		super({ id: 'recruitability', html: recruitability });

		// this.initSlider();
		
		$id('open').disabled = true;
		$id('recruitable').disabled = true;
		$id('closed').disabled = true;

		// this.addListeners();
	}

	/**
	 *
	 */
	addListeners() {
		$id('open' 		 ).addEventListener('change', e => this.openChanged( 		e.target.value));
		$id('recruitable').addEventListener('change', e => this.recruitableChanged( e.target.value));
		$id('closed'	 ).addEventListener('change', e => this.closedChanged( 		e.target.value));
		
		$id('recruitable-slider').noUiSlider.on('update', (values, handle) => this.sliderChanged(values, handle));
	}

	/**
	 *
	 */
	initSlider() {
		// https://refreshless.com/nouislider/examples/#section-html5
		require('nouislider/dist/nouislider.css');
		// let noUiSlider = require('nouislider');

		noUiSlider.cssClasses.target += ' nou';

		noUiSlider.create( 
			$id('recruitable-slider'), 
			{
				...nouiCfg,
				// start: [ 
				// 	cfg.open, 
				// 	cfg.open + cfg.recruitable 
				// ] 
				start: [0.0, 1.0]        	 
			}
		);

		// this.slider.
		let origins = document.querySelectorAll('.noUi-origin');
		origins[1].setAttribute('disabled', true);

	}

	/*
	 INSTEAD of background
		https://refreshless.com/nouislider/examples/#section-colored-connect
		var connect = slider.querySelectorAll('.noUi-connect');
		var classes = ['c-1-color', 'c-2-color', 'c-3-color', 'c-4-color', 'c-5-color'];

		for (var i = 0; i < connect.length; i++) {
		    connect[i].classList.add(classes[i]);
		}

		.c-1-color { background: red; }
		.c-2-color { background: yellow; }
		.c-3-color { background: green; }
		.c-4-color { background: blue; }
		.c-5-color { background: purple; }
	*/
	/**
	 * 	BOGUS
	 * 	Must update the stops on ...
	 *		.nou .noUi-connects {
	 *		background: linear-gradient(red 50%, blue 50%);
	 */

	sliderChanged(values, handle) {
		// console.log('handle = ', handle);
		// console.log('values = ', values);
		let value = values[handle];
		if ( handle ) { // high handle
			$id('closed').value  = Math.floor(100 - 100*values[1])/100;			
			// this.handles = [values[0], 1]; // recursion error ...disable instead

		} else {
			$id('open').value 	 = Math.floor(100*values[0])/100;
		}
		$id('recruitable').value = Math.floor(100*values[1] - 100*values[0])/100;

		Field.dispatcher.dispatchEvent('recruitablilityChanged', this.vals);
	}


	// openChanged(e) {
		// 	let low, high, val;
		// 	[low, high] = getHVals();

		// 	if (this.valueAsNumber < high) {
		// 		val = this.valueAsNumber;
		// 	} else {
		// 		val = 1 + this.valueAsNumber;
		// 		$id('recruitable-slider').noUiSlider.set([val, null]);
		// 	}
		// 	this.setHandle(0, val);
		// }

	// recruitableChanged(e) {
		// 	let low, high, val;
		// 	[low, high] = getHVals();

		// 	if (high < 1) {
		// 		val = low + this.valueAsNumber;
		// 		this.setHandle(1, val);
		// 	} else {
		// 		val = 1 + this.valueAsNumber;
		// 		$id('recruitable-slider').noUiSlider.set([val, null]);
		// 	}
		// }

	// closedChanged(e) {
		// 	// let val = s.recruitable-slider.noUiSlider.get()[1] - s.value;
		// 	$id('recruitable-slider').noUiSlider.set([null, this.valueAsNumber]);		
		// }
	
	/**
	 *
	 */
	openChanged(val) {
		console.log('open = ', val);
		let low, high;
		[low, high] = this.handles;

		if (val < high) {
			this.handles = [val, null];
		} else {
			this.handles = [val, val];
		}
		Field.dispatcher.dispatchEvent('recruitablilityChanged', this.vals);
	}

	/**
	 *
	 */
	recruitableChanged(val) {
		let low, high;
		[low, high] = this.handles;

		if (high < 1) {
			this.handles = [null, val];
		} else {
			this.handles = [val, null];
		}
		Field.dispatcher.dispatchEvent('recruitablilityChanged', this.vals);
	}

	/**
	 *
	 */
	closedChanged(val) {
		// let val = s.recruitable-slider.noUiSlider.get()[1] - s.value;
		this.handles = [null, val];
		Field.dispatcher.dispatchEvent('recruitablilityChanged', this.vals);
	}

	/**
	 *
	 */
	set handles(h) {
		$id('recruitable-slider').noUiSlider.set(h);
		// $id('recruitable-slider').noUiSlider.setHandle(v0, v1, false);
	}

	/**
	 *
	 */
	get handles() {
		return $id('recruitable-slider').noUiSlider.get();
		// let vals = $id('recruitable-slider').noUiSlider.get();
		// return [
		// 	Number(vals[0]),
		// 	Number(vals[1])
		// ];
	}

	/**
	 *
	 */
	set vals(cfg) {
		$id('recruitable').value 	= cfg['recruitable'];
		$id('open').value 			= cfg['open'];
		$id('closed').value 		= cfg['closed'];
	}
	
	/**
	 *
	 */
	get vals(){
		return {
			"open": 		$num('open'),
			"recruitable": 	$num('recruitable'),
			"closed": 		$num('closed')
		};
	}
}
export default Recruitability;