// import JXG from 'jsxgraph';
require("./Memgon.js");
require("./ptIMG_jxg.js");
// require("./JXG_Extensions.js");
require("./JXG_PP_Extensions.js");

import PreisachPlaneHozHighJXG from './PreisachPlaneHozHighJXG.js';
import PreisachPlaneHozLowJXG from './PreisachPlaneHozLowJXG.js';
// require("../css/boards-style.css");

function PreisachPlane(id, atts, dispatcher) {
	// console.log('dispatcher = ', dispatcher);
	// default: horizontal='low'
	// switch() {
	// 	case '':
	// 		break;
	// 	case '':
	// 		break;
	// 	default: ;
	// }
	switch(atts.horizontal) {
		case 'low':
			return new PreisachPlaneHozLowJXG(id, atts, dispatcher);
		case 'high':
			return new PreisachPlaneHozHighJXG(id, atts, dispatcher);
		default: 
			throw new Error("Must have a horizontal value");
	}
};
export default PreisachPlane;