import b from '../../html/branches.html';
require('../../css/branches-style.css');

const EQUATIONS = require('../../json/equations.json');
import {$id, $val, $num} from '../utils/dom_utils';
import Field from './Field.js';

/**
 *
 */
class Branches extends Field {
	/**
	 *
	 */
	constructor() {
		super({ id: 'branches', html: b });
		
		/**
		 * @property equations
		 */
		this.equations = {};
	
		Object.entries(EQUATIONS).forEach( ([k, v], i) => {
			this.createOption(k, v, i);
			this.createEquations(k, v, i);
			// this.createParams(k, v, i);
		});

		// $id('equations').innerHTML = eqs[title]['chtml'];
		$id('eq-select').selectedIndex = 1;

		this.addListeners();
	}

	createOption(k, v, i) {
		let option = document.createElement("option");
		option.text = k;
		option.id = 'opt-'+k;
		$id("eq-select").add(option);
	}

	createEquations(k, v, i) {
		let label = document.createElement("label");
		label.htmlFor = 'opt-'+k;
		label.id = 'eqs-'+k;

		if ( typeof v.chtml === 'undefined' ) {
			document.getElementById('opt-'+k).setAttribute('disabled', '');
		} else {
			label.innerHTML = v.chtml;
		}		
		$id('equations').appendChild(label);
		label.style.display = 'none';
	}

	/**
	 *
	 */
	addListeners() {		
		let updateParam = (evt, key, val) => {
			this[key] = val;

			Field.dispatcher.dispatchEvent('paramChanged', [key, val]);
		}

			// Field.dispatcher.dispatchEvent('equationsChanged', {'equations': eqsJC, 'labels': labels});
		$id("eq-select").addEventListener('change', evt => { this.selected = evt.target.value; Field.dispatcher.dispatchEvent('equationsChanged'); } );

		$id("vmax-slider").addEventListener('input',  evt => { updateParam( evt, 'vmax', evt.target.value ); });
		$id("pmid-slider").addEventListener('input',  evt => { updateParam( evt, 'pmid', evt.target.value ); });
		$id("k-slider"   ).addEventListener('input',  evt => { updateParam( evt, 'k',    evt.target.value ); });
		
		$id("vmax"       ).addEventListener('change', evt => { updateParam( evt, 'vmax', evt.target.value ); });
		$id("pmid"       ).addEventListener('change', evt => { updateParam( evt, 'pmid', evt.target.value ); });
		$id("k"          ).addEventListener('change', evt => { updateParam( evt, 'k',    evt.target.value ); });
	}
	
	set selected(str) {
		// hide old equations
		let el = document.querySelector("#equations>[style='display: block;']");
		if ( el != 'undefined' && el != null ) {
			el.style.display = 'none';
		}

		// show new equations
		// $id("eq-select").value = str;
		$id("eq-select").selected = str;
		$id('eqs-'+str).style.display = 'block';

		if (Object.keys(EQUATIONS).indexOf(str) !== -1) {
			this.equationStrings = {
				'popen': 	EQUATIONS[str]['popen']['jc'],
				'pclosed': 	EQUATIONS[str]['pclosed']['jc'],
				'vopen': 	EQUATIONS[str]['vopen']['jc'],
				'vclosed': 	EQUATIONS[str]['vclosed']['jc']
			};
			
			// update variables
			$id('row-pmid').style.display 	= (str == 'exponential-relaxation') 
											? 'none'
											: 'table-row';

			console.log(EQUATIONS[str]['vars']);
			this.params = EQUATIONS[str]['vars'];
			// let labels = Object.keys(EQUATIONS[str]['vars']);
		} else {
			throw new Error('getUserText not implemented');
		}
	}
	// set selected(str) {}
	get selected() {
		// $id('eq-select').selectedIndex;
		// let str = $id("eq-select").value;
		// console.log(str);
		return $id("eq-select").value;
	}

	set equationStrings(strs) {
		this.equations['popen'] 	= strs['popen'	];
		this.equations['pclosed'] 	= strs['pclosed'];
		this.equations['vopen'] 	= strs['vopen'	];
		this.equations['vclosed'] 	= strs['vclosed'];
	}
	
	get equationStrings() {
		return {
			'popen': 	this.equations['popen'],
			'pclosed': 	this.equations['pclosed'],
			'vopen': 	this.equations['vopen'],
			'vclosed': 	this.equations['vclosed']
		}		
	}
	
	set vmax(val) { 
		$id('vmax').value			= val;
		$id('vmax-slider').value 	= val; 
	}
	set pmid(val) { 
		$id('pmid').value			= val;
		$id('pmid-slider').value 	= val; 
	}
	set k(val) { 
		$id('k'	).value				= val;
		$id('k-slider'	).value 	= val; 
	}

	/**
	 * all that hassle with the select and we still don't have the params updating to the other equations
	 */
	set params(params) {
		Object.entries(params).forEach( ([k, v]) => {
			$id(k+'').value	= v.value;
			$id(k+'').max	= v.max;
			$id(k+'').min	= v.min;
			$id(k+'').step	= v.step;

			$id(k+'-slider').value	= v.value;
			$id(k+'-slider').max	= v.max;
			$id(k+'-slider').min	= v.min;
			$id(k+'-slider').step	= v.step;
		});
		// $id('vmax').value	= params.vmax;
		// $id('pmid').value	= params.pmid;
		// $id('k').value		= params.k;
	}
	
	get params() {
		return {
			'vmax': $num('vmax'),
			'pmid': $num('pmid'),
			'k':	$num('k'   )
		}
	}

	/**
	 * @param {} args
	 */
	set vals(style) {
		this.selected = style;
		// this.selected = style;
		// this.params = EQUATIONS[style].vars;
	}

	get vals() {
		return { 
			"equation-style": 	this.selected,
			"equation-strings": this.equationStrings,
			"params-labels": 	this.paramsLabels,
			"params": 			this.params
		};
	}
}
export default Branches;